.App {
    max-width: 800px;
    margin: 0 auto;
}

.App-nav {
    margin: 20px;
    padding: 10px;
    border-bottom: 2px solid #508fca;
}

.App-nav ul {
    padding: 0;
    margin: 0;

}

.App-nav button {
    font-size: 18px;
    background: transparent;
    border: none;
    cursor: pointer;
}

.app-nav-item {
    list-style: none;
    display: inline-block;
    margin-right: 32px;
    font-size: 24px;
    border-bottom: 4px solid transparent;
}

.app-content {
    margin: 0 20px;
}

.app-nav-item button:hover {
    color: #999;
}

.app-nav-item.selected {
    border-bottom-color: #ffaa3f;
}